import React from 'react';
import axios from 'axios';
import MyLifeComponent from '../component/myLife/myLife';

class MyLifeContainer extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            data: ""
        }
        this.getMyLifeImgs = this.getMyLifeImgs.bind(this);
        this.handleClickReview = this.handleClickReview.bind(this);
    }

    componentDidMount(){
        this.getMyLifeImgs();
    }

    getMyLifeImgs(){
        let that = this;
        return axios.get('/myLife/imgs.json')
            .then(function(result){
                that.setState({
                    data: result.data.data
                })
            })
    }

    handleClickReview(){

    }

    render(){
        return(
            <MyLifeComponent
                data = {this.state.data}></MyLifeComponent>
        )
    }
}

export default MyLifeContainer;